type TextAreaProps = {
    id: string;
    name: string,
    placeholder?: string;
    autoComplete?: boolean;
    rows?: number;
    cols?: number;
}

export default function TextArea({ id, name, placeholder, autoComplete=false, rows=4, cols=50, ...props }: TextAreaProps) {
    return (
        <div className="flex flex-col gap-2 w-200">
            <label htmlFor={id} className="font-medium">
                {name}
            </label>

            <textarea 
                {...props}
                id={id}
                name={name}
                placeholder={placeholder}
                autoComplete={autoComplete? "on" : "off" }
                rows={rows}
                cols={cols}
                className="border border-gray-200 rounded px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
            /> 
        </div>
    );
}