type InputType = "text" | "password" | "email";

type InputProps = {
    id: string;
    name: string,
    type: InputType;
    placeholder?: string;
    autoComplete?: boolean;
}

export default function Input({ id, name, type, placeholder, autoComplete=false, ...props }: InputProps) {
    return (
        <div className="flex flex-col gap-2 w-200">
            <label htmlFor={id} className="font-medium">
                {name}
            </label>

            <input 
                {...props}
                id={id}
                name={name}
                type={type}
                placeholder={placeholder}
                autoComplete={autoComplete? "on" : "off" }
                className="border border-gray-200 rounded px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            /> 
        </div>
    );
}